const names = nameString.split("\n");
names.pop();
names.shift();

const li1 = document.getElementById("name-list-1");
const li2 = document.getElementById("name-list-2");

for (let index = 0; index < names.length; index++) {
  const element = names[index];
  
  const label = document.createElement("label");
  label.setAttribute("class", "list-group-item");

  const checkbox = document.createElement("input");
  checkbox.setAttribute("class", "form-check-input me-1");
  checkbox.setAttribute("type", "checkbox");
  checkbox.setAttribute("value", "");

  label.appendChild(checkbox);
  label.appendChild(document.createTextNode(` ${element} `));

  if (index < names.length / 2)
    li1.appendChild(label);
  else
    li2.appendChild(label);
}